<template>
  <view>
    <!-- 顶部自定义导航栏 -->
    <view :style="{height: statusBarHeight + 'px'}"></view>
    <view class="custom-navbar">
      <view class="back-btn" @click="goBack">
        <image src="/static/3.png" class="back-icon"></image>
      </view>
      <view class="navbar-title-wrap">
        <view class="navbar-title">我的学生</view>
      </view>
      <view class="navbar-placeholder"></view>
    </view>

    <!-- 顶部类型切换tab -->
    <view class="type-tabs">
      <view
        v-for="type in typeList"
        :key="type"
        :class="['type-tab', selectedType === type ? 'active' : '']"
        @click="selectType(type)"
      >{{ type }}</view>
    </view>

    <view class="main-container">
      <!-- 左侧：班课为班课名，其它为年级 -->
      <view class="sidebar">
        <view
          v-for="(item, idx) in leftList"
          :key="item"
          :class="['side-item', selectedLeft === item ? 'active' : '']"
          @click="selectLeft(item)"
        >
          {{ item }}
          <template v-if="classStatusMap[item]">
            <text style="color:#4FC3F7;font-size:24rpx;">（{{ classStatusMap[item].inRead }}人</text>
            <text v-if="classStatusMap[item].isOver" style="color:#f56c6c;font-size:24rpx;">，已结课</text>
            <text style="color:#4FC3F7;font-size:24rpx;">）</text>
          </template>
        </view>
      </view>
      <!-- 右侧学生列表 -->
      <view class="student-panel">
        <view v-if="filteredStudents.length > 0">
          <view 
            class="student-item" 
            v-for="student in filteredStudents" 
            :key="student._id"
            @click="showStudentDetail(student)"
            :class="{'student-over': student.isOver}"
          >
            <view v-if="student.isOver" class="watermark-bg">
              <view class="watermark-text watermark-left">无课时</view>
              <view class="watermark-text watermark-center">无课时</view>
              <view class="watermark-text watermark-right">无课时</view>
            </view>
            <view class="student-info">
              <view class="student-name">新学生：{{ student.studentName }}</view>
              <view class="student-details">
                <text class="grade">{{ student.grade }}</text>
                <text class="subject">{{ student.subject }}</text>
                <text v-if="selectedType === '班课'" class="class-name">{{ student.className }}</text>
              </view>
              <view class="student-meta">
                <text class="hours">剩余课时：{{ student.totalHours }}</text>
                <text class="entrance-score" v-if="student.entranceTestScore">入门测：{{ student.entranceTestScore }}</text>
                <text class="operator-name colored" v-if="student.operatorName">录入人：{{ student.operatorName }}</text>
              </view>
              <view class="date-row start" v-if="student.startDate">开始时间：{{ student.startDate }}</view>
              <view class="date-row end" v-if="student.endDate">结束时间：{{ student.endDate }}</view>
              <view class="remark" v-if="student.remark">备注：{{ student.remark }}</view>
            </view>
            <view class="student-arrow">›</view>
          </view>
        </view>
        <view v-else class="empty-state">
          <text class="empty-text">暂无学生</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      statusBarHeight: 0,
      typeList: ['一对一', '班课', '脱产生'],
      typeMap: {
        '一对一': '1对1',
        '班课': '班课',
        '脱产生': '脱产生'
      },
      selectedType: '班课',
      students: [],
      leftList: [], // 班课名或年级
      selectedLeft: '',
      userInfo: null,
      classStatusMap: {}
    }
  },
  computed: {
    filteredStudents() {
      const realType = this.typeMap[this.selectedType];
      const today = new Date();
      function isOver(s) {
        if (!s.endDate) return false;
        const end = new Date(s.endDate);
        return end.setHours(0,0,0,0) < today.setHours(0,0,0,0);
      }
      let list = [];
      if (realType === '班课') {
        list = this.students.filter(s => s.courseType === '班课' && s.className === this.selectedLeft);
      } else {
        list = this.students.filter(s => s.courseType === realType && s.grade === this.selectedLeft);
      }
      // 给每个学生加 isOver 字段
      return list.map(s => ({ ...s, isOver: isOver(s) }));
    }
  },
  onLoad() {
    this.statusBarHeight = uni.getSystemInfoSync().statusBarHeight;
    this.userInfo = uni.getStorageSync('userInfo');
    this.loadMyStudents();
  },
  methods: {
    goBack() {
      if (getCurrentPages().length > 1) {
        uni.navigateBack();
      } else {
        uni.switchTab({ url: '/pages/home/home' });
      }
    },
    selectType(type) {
      this.selectedType = type;
      this.updateLeftList();
    },
    selectLeft(item) {
      // 允许点击所有班课，包括已结课的班课
      this.selectedLeft = item;
    },
    async loadMyStudents() {
      if (!this.userInfo) {
        uni.showToast({ title: '请先登录', icon: 'none' });
        return;
      }
      try {
        uni.showLoading({ title: '加载中...' });
        const result = await uniCloud.callFunction({
          name: 'student-hours',
          data: {
            action: 'getMyStudents',
            operatorId: this.userInfo._id
          }
        });
        if (result.result.success) {
          this.students = result.result.data || [];
          this.updateLeftList();
        } else {
          uni.showToast({ title: result.result.message || '加载失败', icon: 'none' });
        }
      } catch (error) {
        console.error('加载我的学生失败:', error);
        uni.showToast({ title: '加载失败，请重试', icon: 'none' });
      } finally {
        uni.hideLoading();
      }
    },
    updateLeftList() {
      const realType = this.typeMap[this.selectedType];
      const today = new Date();
      this.classStatusMap = {};
      if (realType === '班课') {
        // 班课名去重
        const classNames = [...new Set(this.students.filter(s => s.courseType === '班课').map(s => s.className).filter(Boolean))];
        // 统计每个班课的在读人数和是否已结课
        // 新的结课判断逻辑：
        // 1. 如果班课中有学生设置了结束日期，以最晚的结束日期作为班课结束时间
        // 2. 如果班课中所有学生都没有设置结束日期，则按在读人数判断
        classNames.forEach(className => {
          const studentsOfClass = this.students.filter(s => s.courseType === '班课' && s.className === className);
          
          // 按时间判断结课：获取班课中所有学生的结束日期，取最晚的结束日期作为班课结束时间
          const endDates = studentsOfClass
            .filter(s => s.endDate) // 只考虑有结束日期的学生
            .map(s => new Date(s.endDate));
          
          let isOver = false;
          if (endDates.length > 0) {
            // 如果班课中有学生设置了结束日期，以最晚的结束日期为准
            const latestEndDate = new Date(Math.max(...endDates));
            isOver = latestEndDate.setHours(0,0,0,0) < today.setHours(0,0,0,0);
          } else {
            // 如果班课中所有学生都没有设置结束日期，则按在读人数判断
            const inRead = studentsOfClass.filter(s => {
              if (!s.startDate || !s.endDate) return true;
              const start = new Date(s.startDate);
              const end = new Date(s.endDate);
              return today >= start && today <= end;
            });
            isOver = inRead.length === 0 && studentsOfClass.length > 0;
          }
          
          // 统计在读人数（用于显示）
          const inRead = studentsOfClass.filter(s => {
            if (!s.startDate || !s.endDate) return true;
            const start = new Date(s.startDate);
            const end = new Date(s.endDate);
            return today >= start && today <= end;
          });
          
          this.classStatusMap[className] = {
            total: studentsOfClass.length,
            inRead: inRead.length,
            isOver: isOver
          };
        });
        this.leftList = classNames;
        // 默认选中第一个在读班课，否则第一个班课
        const firstInRead = classNames.find(cn => this.classStatusMap[cn].inRead > 0);
        this.selectedLeft = firstInRead || (classNames[0] || '');
      } else {
        // 年级去重
        const grades = [...new Set(this.students.filter(s => s.courseType === realType).map(s => s.grade).filter(Boolean))];
        this.leftList = grades;
        this.selectedLeft = grades.length > 0 ? grades[0] : '';
      }
    },
    showStudentDetail(student) {
      let content = `姓名：${student.studentName}\n`;
      if (this.selectedType === '班课') {
        content += `班课：${student.className}\n`;
      } else {
        content += `年级：${student.grade}\n科目：${student.subject}\n`;
      }
      content += `剩余课时：${student.totalHours}${student.entranceTestScore ? `\n入门测成绩：${student.entranceTestScore}` : ''}${student.remark ? `\n备注：${student.remark}` : ''}${student.startDate ? `\n开始时间：${student.startDate}` : ''}${student.endDate ? `\n结束时间：${student.endDate}` : ''}`;
      uni.showModal({
        title: '学生详情',
        content: content,
        showCancel: false,
        confirmText: '确定'
      });
    }
  }
}
</script>

